export default{
	data(){
		return{
			isZhibo:true,//直播
			isJiaocheng:false,//教程
			tabarIndex:1,
		}
	},
	methods:{
		//切换tabar
		changeTabar(){
			if(this.tabarIndex == 1){
				this.isZhibo = true;
				this.isJiaocheng = false;
				this.tabarIndex = 2;
			}else{
				this.isZhibo = false;
				this.isJiaocheng = true;
				this.tabarIndex = 1;
			}
				
		}
	}
}
